.profile__avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-right: 30px;
    object-fit: cover;
    transition: opacity 1s ease 0s;
    position: relative;
    z-index: -1;
}

@media screen and (max-width: 899px) {
    .profile__avatar {
        align-self: center;
        margin: 0;
    }
}